html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

body {
  text-align: center;
}

html {
  font-family: "Libre Franklin", sans-serif;
}

body {
  font-size: 1.25rem;
}

header {
  max-width: 40rem;
  margin: auto;
}
@media (max-width: 39.9375em) {
  header {
    max-width: 20rem;
  }
}
header img {
  margin-top: 5.4375rem;
  margin-bottom: 2.8125rem;
}
@media (max-width: 39.9375em) {
  header img {
    margin-top: 5.3125rem;
    margin-bottom: 2.1875rem;
    height: 1rem;
  }
}

main {
  max-width: 40rem;
  margin: auto;
}
@media (max-width: 39.9375em) {
  main {
    max-width: 20rem;
  }
}
main .text {
  margin-bottom: 2.5rem;
}
@media (max-width: 39.9375em) {
  main .text {
    margin-bottom: 2rem;
  }
}
main .text--title {
  color: #969696;
  font-weight: 300;
  font-size: 3rem;
  margin-bottom: 1.5625rem;
}
@media (max-width: 39.9375em) {
  main .text--title {
    font-size: 1.375rem;
    margin-bottom: 1.125rem;
  }
}
main .text--title span {
  color: #151f29;
  font-weight: 700;
}
main .text--para {
  color: #151f29;
  font-weight: 300;
}
@media (max-width: 39.9375em) {
  main .text--para {
    font-size: 0.75rem;
  }
}
main .input {
  margin-bottom: 5.625rem;
}
@media (max-width: 39.9375em) {
  main .input {
    margin-bottom: 4.5rem;
  }
}
main .input form {
  width: 100%;
  display: flex;
  gap: 0.9375rem;
  flex-wrap: wrap;
}
main .input--text {
  /* font-family: 'Libre Franklin', sans-serif;
  font-size: rem(16);
  font-weight: 300;
  padding: rem(18) rem(30);
  outline: none;
  border: rem(1), solid, $pale-blue;
  border-radius: rem(30);
  width: 100%; */
  flex: 65%;
}
main .input--text--input {
  font-family: "Libre Franklin", sans-serif;
  font-size: 1rem;
  font-weight: 300;
  padding: 1.125rem 1.875rem;
  outline: none;
  border: 0.0625rem, solid, #c2d3ff;
  border-radius: 1.875rem;
  width: 100%;
}
main .input--text--input::placeholder {
  font-family: "Libre Franklin", sans-serif;
  color: #c2d3ff;
}
main .input--text--error {
  display: none;
  font-size: 0.75rem;
  text-align: left;
  padding: 0.625rem 0 0 1.875rem;
  font-style: italic;
  color: #ff5263;
}
@media (max-width: 39.9375em) {
  main .input--text--error {
    text-align: center;
  }
}
main .input--text .error {
  border-color: #ff5263;
}
main .input--button {
  font-family: "Libre Franklin", sans-serif;
  color: white;
  background-color: #4f7df3;
  font-size: 1rem;
  font-weight: 600;
  padding: 1.125rem 1.875rem;
  outline: none;
  border: 0.0625rem, solid, #c2d3ff;
  border-radius: 1.875rem;
  width: 100%;
  max-height: 3.5625rem;
  flex: 27%;
}
main .input--button:hover {
  filter: opacity(75%);
  cursor: pointer;
}
main .image img {
  height: 24.0625rem;
  width: 40rem;
}
@media (max-width: 39.9375em) {
  main .image img {
    height: 12.03125rem;
    width: 20rem;
  }
}

footer {
  position: absolute;
  max-width: 40rem;
  margin: auto;
  bottom: 3.125rem;
  left: 50%;
  transform: translateX(-50%);
  color: #969696;
  font-size: 0.75rem;
  font-weight: 300;
}
@media (max-width: 39.9375em) {
  footer {
    max-width: 20rem;
    bottom: 2.1875rem;
    font-size: 0.625rem;
  }
}
footer .logos {
  margin-bottom: 1.375rem;
}
footer .logos .logo {
  margin: 0 0.375rem;
  display: inline;
}
footer .logos .logo ion-icon {
  font-size: 0.875rem;
  padding: 0.4375rem;
  color: #4f7df3;
  border: 0.0625rem, solid, #c2d3ff;
  border-radius: 50%;
}
footer .logos .logo ion-icon:hover {
  color: white;
  background-color: #4f7df3;
  cursor: pointer;
}